* {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

html, body {
  height: 100%;
  width: 100%;
}

.preco-destaque {
  text-transform: uppercase;
  text-align: center;
  font-family: fantasy;
  font-size: 2.5rem;
  font-weight: 500;
  -webkit-animation: 1.4s infinite alternate aumenta-diminui;
  animation: 1.4s infinite alternate aumenta-diminui;
  display: block;
}

.aumenta_e_shadow {
  transition: all .5s;
  -webkit-transform: scale(1.5);
  transform: scale(1.5);
  box-shadow: 0 0 2px 2px #000;
}

.texto-invisivel {
  width: 1px;
  height: 1px;
  color: rgba(0, 0, 0, 0);
  position: absolute;
  top: -500px;
}

.container_invisivel {
  opacity: 0;
  display: none;
}

.visificador {
  opacity: 1 !important;
  display: block !important;
}

.botao-ativado {
  opacity: 1;
  transition: all .5s;
}

.botao-ativado:hover {
  cursor: pointer;
  -webkit-transform: scale(1.05);
  transform: scale(1.05);
  box-shadow: 0 0 6px 4px #fff;
}

.botao-desativado {
  opacity: .5;
}

.delete-icon {
  font-family: Courier New, Courier, monospace;
  font-size: 1.25rem;
  font-weight: bold;
  display: inline-block;
  position: absolute;
  top: 10px;
  right: 10px;
}

.delete-icon:hover {
  cursor: pointer;
}

input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

@-webkit-keyframes aumenta-diminui {
  0% {
    -webkit-transform: rotate(-5deg);
    transform: rotate(-5deg);
  }

  40%, 60% {
    -webkit-transform: rotate(0)scale(1.1);
    transform: rotate(0)scale(1.1);
  }

  100% {
    -webkit-transform: rotate(5deg);
    transform: rotate(5deg);
  }
}

@keyframes aumenta-diminui {
  0% {
    -webkit-transform: rotate(-5deg);
    transform: rotate(-5deg);
  }

  40%, 60% {
    -webkit-transform: rotate(0)scale(1.1);
    transform: rotate(0)scale(1.1);
  }

  100% {
    -webkit-transform: rotate(5deg);
    transform: rotate(5deg);
  }
}

